<template>
  <div
    class="d-flex justify-center align-center flex-grow-1 flex-sm-grow-0 full-height"
    :class="{ 'content-wrapper': !$root.isMobile }"
  >
    <div
      class="content-wrapper d-flex flex-column align-center white border-radius justify-space-between justify-sm-start px-5 pb-10 pa-sm-12"
      :class="{ 'box-shadow': !$root.isMobile }"
    >
      <div
        class="d-flex flex-column align-self-stretch pt-16 pt-sm-0"
      >
        <h1 class="text-center">
          Forgot password?
        </h1>
        <p class="d-flex body-2 font-weight-medium primary-grey--text mb-5">
          Just fill in your email and we'll send you a temporary code to reset password.
        </p>
        <v-form
          ref="form"
          class="form"
          style="width: 100%;"
          v-model="form.valid"
          lazy-validation
          @keyup.enter.native="handleSubmit"
        >
          <base-field-input
            label="Email"
            :is-required="true"
            autocomplete="user-email"
            type="email"
            placeholder="Enter Email"
            v-model="form.values.email"
            :rules="form.rules.email"
            :error="Boolean(serverErrorMessage)"
            :error-messages="serverErrorMessage"
          />
        </v-form>

        <div class="d-flex justify-space-between mt-3">
          <v-btn
            color="primary"
            class="text-uppercase custom__btn"
            depressed
            outlined
            :to="{ name: this.$constants.ROUTE_NAMES['SIGN_IN']}"
            min-width="215"
            large
            @click="toBack"
          >
            <img
              class="account-icon mr-2"
              :src="require('@images/icons/drivers/back.svg')"
            />
            <span class="font-weight-bold">GO BACK</span>
          </v-btn>
          <v-btn
            large
            depresse
            color="primary"
            class="text-uppercase custom__btn"
            min-width="215"
            :disabled="!form.valid || !form.values.email"
            :loading="submitting"
            :block="$root.isMobile"
            @click="handleSubmit"
          >
            Send code
          </v-btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from 'vuex';
import { actions } from '@modules/auth/constants';
import BaseFieldInput from '@components/BaseFieldInput';
import { required, email } from '@utils/helpers/validationRules';

const { mapActions, mapState } = createNamespacedHelpers('auth');

export default {
  name: 'ForgotPassword',
  components: {
    BaseFieldInput,
  },
  props: {
    role: {
      type: String,
      required: true,
    },
  },
  watch: {
    'form.values': {
      deep: true,
      handler() {
        this.form.serverErrorMessage = '';
      },
    },
  },
  data() {
    return {
      form: {
        valid: true,
        submitted: false,
        serverErrorMessage: '',
        values: {
          email: '',
        },
        rules: {
          email: [required('Email'), email],
        },
      },
    };
  },
  computed: {
    serverErrorMessage() {
      if (this.form.serverErrorMessage) {
        return this.form.serverErrorMessage;
      }

      return null;
    },
    ...mapState({
      submitting: (state) => state.isLoading,
    }),
  },
  methods: {
    handleSubmit() {
      const isFormValid = this.$refs.form.validate();
      if (!isFormValid) return;
      this.forgotPassword({ role: this.role, email: this.form.values.email })
        .then(() => {
          this.$router.push({ name: this.$constants.ROUTE_NAMES.RESET_PASSWORD, params: { role: this.role } });
          sessionStorage.setItem('email', this.form.values.email);
        }).catch((errorMessage) => {
          this.form.serverErrorMessage = errorMessage;
        });
    },
    ...mapActions({
      forgotPassword: actions.FORGOT_PASSWORD,
    }),
  },
};
</script>

<style lang="scss" scoped>
  .form,
  .autoreply {
    width: 100%;

    @media #{map-get($display-breakpoints, 'sm-and-up')}{
      width: 450px;
    }
  }
  .content-wrapper {
    @media #{map-get($display-breakpoints, 'xs-only')}{
      width: 100%;
    }
  }
  .autoreply-icon {
    transform: translateX(-26px);
  }
</style>
